@import url(https://fonts.googleapis.com/css2?family=Spartan:wght@400;500;600&display=swap);
*{font-family:'Spartan', sans-serif;font-size:16px;background:#ffffff}.app-wrapper{margin:15px auto;width:360px;display:flex;justify-content:center;align-items:center}

.list-wrapper-flex{display:flex;align-items:center;justify-content:space-between;margin:20px 0px}.list-wrapper-flex .list-info .price{font-size:12px;font-weight:700}.list-wrapper-flex .list-info .description{font-size:08px}.list-wrapper-flex .img-add .img-card{position:relative}.list-wrapper-flex .img-add .img-card img{border:1px solid #e8e9eb;border-radius:5px;object-fit:contain}.list-wrapper-flex .img-add button{width:90px;position:absolute;bottom:-10px;right:30px;border:2px solid #e8e9eb;color:#008100;font-size:14px;font-weight:700;padding:08px 10px;border-radius:05px;box-shadow:2px 5px 10px #e8e9eb;cursor:pointer}.list-wrapper-flex .img-add button:hover{transform:translateY(2px);color:#026302}.list-wrapper-flex .img-add p{padding:05px;font-size:09px;font-weight:700;color:#92939d;text-align:center}.search-flex{display:flex;align-items:center;margin:10px 0px}.search-flex .back{margin:0px 05px}.search-flex .search-input input{width:330px;border:none;line-height:24px;height:30px}.search-flex .search-input input:focus{outline:none;border:none}

